import { _decorator, Component, Node, Sprite, resources, SpriteFrame, Layers, find, Vec2, BoxCollider2D, Collider2D } from 'cc';
import { canvas } from './canvas';
import { infoManeger } from './infoManeger';
import { PaoState } from './PaoState';
import { toolPyManeger } from './toolPyManeger';
import { ToolStateManeger } from './ToolStateManeger';
const { ccclass, property } = _decorator;

@ccclass('selectToolManeger')
export class selectToolManeger extends Component {
    @property
    tooltype:number = 0;
    @property
    toolname = "null";

    @property
    selectStep:number = 0;
    //0为未选中
    //1为选中开始移动但与碰撞体为碰撞
    //2为与碰撞体碰撞

    @property
    targetBlock:string;

    start() {
        this.node.on(Node.EventType.MOUSE_UP, (event) => {
            find("Canvas/infobox").getComponent(infoManeger).setInfoBox(this.tooltype)
          }, this);
    }

    update(deltaTime: number) {
        this.node.on(Node.EventType.TOUCH_MOVE,this.SelectToolAndMove,this);
        this.node.on(Node.EventType.TOUCH_CANCEL,this.SelectToolAndCancel,this)
    }

    SelectToolAndMove(event){
        if (find("canvas/selectToolBox").getChildByName(this.toolname)){
            console.log('炮节点已存在，正在移动')
            let currentNode = this.node.parent.parent.parent.getChildByName(this.toolname);
            let pos = new Vec2();
            let shit = pos.set(event.getUILocation())
            let x = shit.x - 800;
            let y = shit.y - 450;
            //转换为UI坐标
            // pos=currentNode.parent.getComponent(UITransform).convertToNodeSpaceAR(pos);
            // console.log(pos);
            currentNode.setPosition(x,y,0);
            // this.node.parent.parent.getChildByName('pao1').destroy();
        }else{
        //TODO:同时使用多个炮的时候可能会造成冲突
            let node = new Node(this.toolname);
            node.addComponent(Sprite);
            let spriteFramePath = 'pao/spriteFrame';
            if (this.toolname == "pao"){
                spriteFramePath = "sanshepao/spriteFrame"
            } else if (this.toolname == "dan"){
                spriteFramePath = "zhuizongdan/spriteFrame"
            } else if (this.toolname == "guangzi" || "jian"){
                spriteFramePath = "guangzijian/spriteFrame"
            } else {
                spriteFramePath = 'pao/spriteFrame';
            }
            resources.load(spriteFramePath,SpriteFrame,(_err,spriteFrame)=>{
                node.getComponent(Sprite).spriteFrame = spriteFrame;
                //在cocos3x版本中，layer默认为default，node.layer = Layers.Enum.UI_2D；
                //2d摄像头可见的是UI_2D和UI_3D
                node.layer = Layers.Enum.UI_2D;
                node.setScale(0.3,0.3,1);
                //添加碰撞组件
                node.addComponent(BoxCollider2D);
                node.getComponent(BoxCollider2D).group = 4;
                console.log(node.getComponent(BoxCollider2D))
                node.addComponent(toolPyManeger);
                
                console.log('炮创建成功');
            })
            //放在战斗组，目前还没定义战斗组，所有放在顶层；
            node.parent = this.node.parent.parent.parent;
            
        }
    }

    SelectToolAndCancel(event){
        console.log('已取消触摸事件')
        let nodePao = this.node.parent.parent.parent.getChildByName(this.toolname);
        
        let step = this.selectStep;
        console.log("step:",step);
        if(!(step==2)){
            console.log('离开碰撞且取消触摸事件')
            nodePao.destroy();
        }else{
            // nodePao.parent = find()
            let parentName = nodePao.getComponent(PaoState).ToolStateManeger.master
            console.log('这个node从属于',nodePao.getComponent(PaoState).ToolStateManeger.master)
            console.log(nodePao.parent.getChildByName("left").getChildByName(parentName))

            //TODO:增加逻辑；若该组件下面挂在了一个tool了，就不允许再创建新的tool；
            let NewNode = this.NewPao();
            NewNode.parent = this.node.parent.parent.parent.getChildByName("left").getChildByName(parentName);
            NewNode.active = true;
            nodePao.destroy();
            // nodePao.setPosition(100,100,0)
            // console.log(nodePao.parent);

        }
    }
}


